body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 90%;
    margin: auto;
    display: flex;
    align-items: center;
}

.header a {
    color: black;
    text-decoration: none;
    font-weight: bold;
    font-size: 1.25vmax;
    padding: 40px 20px;
    transition: .3s;
}

/* MOBILE HEADER */
#open {
    color: #222222;
    font-size:25px; 
    cursor:pointer; 

    display: none;
}

#overlay { 
    height: 100%; 
    width: 0; 
    position: fixed; 
    z-index: 1; 
    top: 0; 
    left: 0; 
    background: #FAFAFA; 
    overflow-x: hidden; 
    transition: 0.3s; 
} 

#links {
    position: relative; 
    top: 10%; 
    width: 100%; 
    padding-left: 10%;
}

#overlay #close {
    width: 80%;
    height: 100px;
    margin: auto;
    display: flex;
    align-items: center;

    font-size: 25px; 
    color: #222222;
    cursor: pointer;
}

#overlay a {
    padding: 8px; 
    text-decoration: none; 
    font-size: 45px; 
    color: #222222; 
    display: block;
    border: none;
    background: none;
    cursor: pointer;
}


@media (max-width: 500px) {
    .header a { font-size: 2vmax; }
    #open { display: block; }
    .normal_header { display: none; }
}
@media (max-width: 900px) and (orientation: landscape)  {
    .header a { font-size: 2vmax; }
    #open { display: block; }
    .normal_header { display: none; }
}
